import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { Router } from '@angular/router';
import { vendors } from '../model/vendor';
import { environment } from 'src/environments/environment';
import { HttpClient, HttpHeaders } from '@angular/common/http';




@Injectable({
  providedIn: 'root'
})
export class ViewVendorsService {
  vendor:vendors;


  constructor(private _httpService:HttpClient,private router:Router) { }
  
  getAllvendors(): Observable<vendors[]>{
    return this._httpService.get<vendors[]>(environment.APIUrl+'/vendor/null')
  }
  //disable medicine service
  disableVendors(searchId: any): any{
    let body = JSON.stringify(searchId);
    let headers = new HttpHeaders({'Content-Type':'application/json'});
    let options = { headers: headers }

      return this._httpService.put<vendors[]>(environment.APIUrl+'/disablevendor/'+searchId, body, options);



  }
  getVendorbyId(vId:any): Observable<vendors> {
    return this._httpService.get<vendors>(environment.APIUrl +'/vendors/'+ vId)
  }
}




