import { Component, OnInit } from '@angular/core';
import { ViewVendorsService } from '../services/view-vendors.service';
import { Router } from '@angular/router';
import { vendors } from '../model/vendor';

@Component({
  selector: 'app-view-vendor',
  templateUrl: './view-vendor.component.html',
  styleUrls: ['./view-vendor.component.scss']
})
export class ViewVendorComponent implements OnInit {
  viewVendors: vendors[];
  viewVendor = new vendors();
  searchId: any;


  constructor(private _viewvendorService:ViewVendorsService ,private router:Router) { }

  ngOnInit() {
    this.getVendors();
  }
  //get all staff
getVendors(): void{
  this._viewvendorService.getAllvendors().subscribe((vendorList)=>{
    this.viewVendors=vendorList,
    console.log(vendorList);
  },(error)=>{
    console.log(error);
  });


}
//selecting  id for disable
onSelect(selectedVendor: any){
  console.log("selected Item Id: ", selectedVendor.Id);
  this.searchId=selectedVendor.vendorId;
}


//disable medicine
disableVendor(){

  this._viewvendorService.disableVendors(this.searchId)
  .subscribe((vendorsData)=>{
    console.log(vendorsData);
    this.getVendors();
    
  },(error)=>{
    console.log(error);
  });
}
edit_Vendor(data:any){
  this.router.navigate(['addVendor',data]);
  
}
addVendor() {
  this.router.navigate(['addVendor']);
};
view_Vendor(id:any) {
  this.router.navigate(['viewContact',id]);
};
logout(){
  this.router.navigate(['']);
};



}
