import { Component, OnInit } from '@angular/core';

import { ActivatedRoute, Router } from '@angular/router';
import { ViewVendorsService } from '../services/view-vendors.service';
import { vendors } from '../model/vendor';


@Component({
  selector: 'app-view-contact-details',
  templateUrl: './view-contact-details.component.html',
  styleUrls: ['./view-contact-details.component.scss']
})
export class ViewContactDetailsComponent implements OnInit {

  constructor(private _service:ViewVendorsService,private route:ActivatedRoute,private router:Router) { }

  ngOnInit() {

    this.getVendorById();
    this.resetform();

  }

  vId:any;
  vendor = new vendors();

  resetform() {
    this._service.vendor = {
      vendorId: null,
      vendorName: '',
      address: '',
      location: '',
      service: '',
      pincode: '',
      contactid: '',
      name: '',
      dept: '',
      email: '',
      phone: '',
      isActive: null
    }
  }

  getVendorById(){
    this.resetform();
    this.vId=this.route.snapshot.paramMap.get('id');
    this._service.getVendorbyId(this.vId)
    .subscribe((data) =>{
      this.vendor=data;
      console.log('inside');
      console.log(data);
      
      console.log(this.vendor.vendorName);
    },(error)=>{
      console.log(error);
    })
  }
  logout(){
    this.router.navigate(['']);
  };

}
