import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { AddVendorService } from '../services/add-vendor.service';
import { vendors } from '../model/vendor';

@Component({
  selector: 'app-add-vendor',
  templateUrl: './add-vendor.component.html',
  styleUrls: ['./add-vendor.component.scss']
})
export class AddVendorComponent implements OnInit {

  vendor = new vendors();
  vId:any;

  constructor(private router: Router,private route:ActivatedRoute,private _service: AddVendorService) { }

  ngOnInit() {
    this.getVendorById();
  }

  resetform() {
    this._service.vendor = {
      vendorId: null,
      vendorName: '',
      address: '',
      location: '',
      service: '',
      pincode: '',
      contactid: '',
      name: '',
      dept: '',
      email: '',
      phone: '',
      isActive: null
    }
  }


  saveVendor() {
    this.resetform();
    this._service.insertVendor(this.vendor)
    .subscribe((response)=>{
      
      console.log(response);
      // this.toastr.success('Saved successfully');
      this.router.navigate(['viewVendor'])
    }, (error)=>{
      console.log(error);
    });
  }

  getVendorById(){
    this.vId=this.route.snapshot.paramMap.get('id');
    this._service.getVendorbyId(this.vId)
    .subscribe((data)=>{
      this.vendor=data;
      console.log(data);
    },(error)=>{
      console.log(error);
    })
  }
}
