import { Injectable } from '@angular/core';
import { vendors } from '../model/vendor';

import { environment } from 'src/environments/environment';
import { HttpHeaders, HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class AddVendorService {


  vendor:vendors;
  constructor(private httpService:HttpClient) { }


  insertVendor(vendor: vendors) {
    let body = JSON.stringify(vendor);
    console.log(vendor);
    let headers = new HttpHeaders({ 'Content-Type': 'application/json' });
    let options = { headers: headers }
    console.log('before save');
    // return this._httpService.post(environment.APIUrl+'insertstaff/'+formData.roleName ,body, options);

    if (vendor.vendorId) {
      console.log(vendor.vendorId)
      return this.httpService.put(environment.APIUrl+ '/updatevendor', body, options);
    } else {
      return this.httpService.post(environment.APIUrl + '/insertvendor', body, options);
    }
  }

  getVendorbyId(vId: any): Observable<vendors> {
    return this.httpService.get<vendors>(environment.APIUrl + '/vendors/' + vId)
  }
}
